.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.logo {
  padding-left: 80%;
  font-size: 20px;
}

.list1 {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
  background: rgb(177, 125, 235);
  width: 100%;
}

.list2 {
  padding-left: 20px;
  text-align: left;
  color: rgb(254, 190, 138);
  border-bottom: 1px solid white;
  background: rgb(223, 142, 227);
  width: 100%;
}

.box {
  border: 2px solid red;
  width: 26%;
}

.list3 {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
  border-top: 1px solid black;
  background: skyblue;
  width: 100%;
  margin-top: 20px;
  font-style: italic;
}

